<template>
  <div
    class="absolute left-0 right-0 bg-transparent p-2 pb-0"
    :style="styles"
    @dragstart="onDragStart"
    @dragend="onDragEnd"
    @mouseenter="onMouseEnter"
    @mouseleave="onMouseLeave"
    draggable
  >
    <div class="shadow rounded p-2 h-full cursor-move" :class="classes" v-html="title" />
    <div
      v-if="shouldShowTooltip"
      class="absolute top-0 left-0 m-2 p-2 bg-white shadow rounded z-50"
      v-html="fullTitle"
    />
    <div class="absolute top-0 right-0 m-2 p-2 cursor-pointer" title="Rozdělit" @click="onSplit">
      <svg
        xmlns="http://www.w3.org/2000/svg"
        class="icon icon-tabler icon-tabler-copy"
        width="20"
        height="20"
        viewBox="0 0 24 24"
        stroke-width="1.5"
        stroke="#000000"
        fill="none"
        stroke-linecap="round"
        stroke-linejoin="round"
      >
        <path stroke="none" d="M0 0h24v24H0z" fill="none" />
        <rect x="8" y="8" width="12" height="12" rx="2" />
        <path d="M16 8v-2a2 2 0 0 0 -2 -2h-8a2 2 0 0 0 -2 2v8a2 2 0 0 0 2 2h2" />
      </svg>
    </div>

    <v-modal ref="modal" />
  </div>
</template>

<script>
import { mapMutations } from 'vuex';
import { Block } from '../models/Block';
import { Column } from '../models/Column';
import Modal from './Modal';

export default {
  components: {
    'v-modal': Modal,
  },

  props: {
    block: {
      type: Block,
      required: true,
    },
    column: {
      type: Column,
      required: true,
    },
  },

  data: () => ({
    isMouseOver: false,
  }),

  mounted() {
    this.initBlock({
      block: this.block,
      el: this.$el,
    });
  },

  computed: {
    classes() {
      return [this.block.color, { 'opacity-50 border border-blue-400': this.block.isDragging }];
    },

    styles() {
      return {
        height: `${this.block.height * 4}rem`,
        top: `${this.block.top * 4}rem`,
      };
    },

    title() {
      return this.isSmall ? this.shortTitle : this.fullTitle;
    },

    shortTitle() {
      return this.block.title.split('<br>')[0];
    },

    fullTitle() {
      return this.block.title + `<br>${this.block.amount} ks`;
    },

    isSmall() {
      return this.block.height < 2.2;
    },

    shouldShowTooltip() {
      return this.isMouseOver && this.isSmall;
    },
  },

  methods: {
    ...mapMutations(['initBlock', 'dragStart', 'dragEnd', 'split']),

    onDragStart(e) {
      e.dataTransfer.setDragImage(new Image(), 0, 0);

      this.dragStart({
        block: this.block,
      });
    },

    onDragEnd() {
      this.dragEnd({
        block: this.block,
      });
    },

    onMouseEnter() {
      this.isMouseOver = true;
    },

    onMouseLeave() {
      this.isMouseOver = false;
    },

    async onSplit() {
      const amount = await this.$refs.modal.show({
        max: this.block.amount,
      });

      if (amount === null) return;

      this.split({
        block: this.block,
        column: this.column,
        amount,
      });
    },
  },
};
</script>
