<template>
  <div class="bg-gray-100 min-h-screen p-4">
    <div class="flex">
      <v-column v-for="column in queueColumns" :key="column.id" :column="column" />
      <v-timeline />
      <v-column v-for="column in regularColumns" :key="column.id" :column="column" />
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import Column from './components/Column';
import Timeline from './components/Timeline';

export default {
  components: {
    'v-column': Column,
    'v-timeline': Timeline,
  },

  computed: {
    ...mapState(['columns']),

    queueColumns() {
      return this.columns.filter(column => column.id === 'queue');
    },

    regularColumns() {
      return this.columns.filter(column => column.id !== 'queue');
    },
  },
};
</script>
