import BlockComponent from '../components/Block';

export class Block {
  constructor({ id, title, work = 1, amount = 1, color = 'bg-white', blocked = [] }) {
    /** @type {string} */
    this.id = id;
    /** @type {Element} */
    this.el = null;
    /** @type {string} */
    this.title = title;
    /** @type {number} */
    this.work = work;
    /** @type {number} */
    this.amount = amount;
    /** @type {string} */
    this.color = color;
    /** @type {string[]} */
    this.blocked = blocked;
    /** @type {number} */
    this.top = 0;
    /** @type {number} */
    this.height = 4;
    /** @type {boolean} */
    this.isDragging = false;
  }

  getNextOffset() {
    return this.top + this.height;
  }

  get component() {
    return BlockComponent;
  }
}
