<template>
  <div class="bg-gray-400 shadow rounded pb-2 mr-4 w-64" @dragover.prevent="onDragOver">
    <div class="bg-white p-2 rounded-t">{{ column.title }}</div>

    <div class="relative" :style="`height: ${column.height * 4}rem`">
      <component v-for="block in column.blocks" :key="block.id" :is="block.component" :block="block" :column="column" />
    </div>
  </div>
</template>

<script>
import { mapMutations } from 'vuex';
import throttle from 'lodash/throttle';
import { Column } from '../models/Column';

const THROTTLE = 100;

export default {
  props: {
    column: {
      type: Column,
      required: true,
    },
  },

  mounted() {
    this.initColumn({
      column: this.column,
    });
  },

  methods: {
    ...mapMutations(['initColumn', 'dragOver']),

    onDragOver: throttle(function(e) {
      this.dragOver({
        column: this.column,
        y: e.clientY,
      });
    }, THROTTLE),
  },
};
</script>
