import { Block } from './Block';
import FixedBlockComponent from '../components/FixedBlock';

export class FixedBlock extends Block {
  constructor({ id, title, top, height }) {
    super({
      id,
      title,
    });

    /** @type {number} */
    this.top = top;
    /** @type {number} */
    this.height = height;
  }

  get component() {
    return FixedBlockComponent;
  }
}
