/**
 * @typedef {import('./Block').Block} Block
 */

import { FixedBlock } from './FixedBlock';

export class Column {
  constructor({ id, title, effectivity = 1, blocks = [] }) {
    /** @type {string} */
    this.id = id;
    /** @type {string} */
    this.title = title;
    /** @type {number} */
    this.effectivity = effectivity;
    /** @type {Block[]} */
    this.blocks = blocks;
    /** @type {number} */
    this.height = 0;
  }

  recalculate() {
    const bannedRegions = this._getBannedRegions();

    let offset = 0;

    for (const block of this.blocks) {
      if (block instanceof FixedBlock) continue;

      block.top = offset;
      block.height = Math.max(Math.ceil((block.work * block.amount) / this.effectivity), 1);

      // eslint-disable-next-line no-constant-condition
      while (true) {
        const { to } = this._getIntersectingBannedRegion(bannedRegions, block);

        if (to) {
          block.top = to;
        } else {
          break;
        }
      }

      offset = block.getNextOffset();
    }

    this.blocks.sort((a, b) => a.top - b.top);

    this.height = offset;
  }

  _getBannedRegions() {
    return this.blocks
      .filter(block => block instanceof FixedBlock)
      .map(block => ({ from: block.top, to: block.top + block.height }));
  }

  _getIntersectingBannedRegion(bannedRegions, block) {
    for (const bannedRegion of bannedRegions) {
      const blockFrom = block.top;
      const blockTo = block.top + block.height;

      if (blockFrom === bannedRegion.from) return bannedRegion;
      if (blockTo === bannedRegion.to) return bannedRegion;

      if (blockFrom > bannedRegion.from && blockFrom < bannedRegion.to) return bannedRegion;
      if (blockTo > bannedRegion.from && blockTo < bannedRegion.to) return bannedRegion;

      if (blockFrom < bannedRegion.from && blockTo > bannedRegion.to) return bannedRegion;
    }

    return {};
  }
}
