<template>
  <v-portal v-if="isShown">
    <div class="fixed z-50 inset-0 overflow-y-auto">
      <div class="flex items-end justify-center min-h-screen pt-4 px-4 pb-20 text-center sm:block sm:p-0">
        <span class="hidden sm:inline-block sm:align-middle sm:h-screen"></span>&#8203;

        <div
          class="inline-block align-bottom bg-white rounded-lg text-left overflow-hidden shadow-xl sm:my-8 sm:align-middle sm:max-w-lg sm:w-full"
          role="dialog"
          aria-modal="true"
          aria-labelledby="modal-headline"
        >
          <div class="bg-white px-4 pt-5 pb-4 sm:p-6 sm:pb-4">
            <div class="sm:flex sm:items-start">
              <div class="mt-3 text-center sm:mt-0 sm:ml-4 sm:text-left">
                <h3 class="text-lg leading-6 font-medium text-gray-900" id="modal-headline">
                  Rozdělit práci (původní {{ max }} ks)
                </h3>
                <div class="mt-2">
                  <input
                    ref="input"
                    v-model.number="amount"
                    class="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline"
                    type="number"
                    placeholder="Počet ks"
                    @keypress.enter="onConfirm"
                  />
                </div>
              </div>
            </div>
          </div>
          <div class="bg-gray-50 px-4 py-3 sm:px-6 sm:flex sm:flex-row-reverse">
            <span class="flex w-full rounded-md shadow-sm sm:ml-3 sm:w-auto">
              <button
                type="button"
                class="inline-flex justify-center w-full rounded-md border border-transparent px-4 py-2 bg-green-600 text-base leading-6 font-medium text-white shadow-sm hover:bg-green-500 focus:outline-none focus:border-green-700 focus:shadow-outline-green transition ease-in-out duration-150 sm:text-sm sm:leading-5"
                @click="onConfirm"
              >
                Rozdělit
              </button>
            </span>
            <span class="mt-3 flex w-full rounded-md shadow-sm sm:mt-0 sm:w-auto">
              <button
                type="button"
                class="inline-flex justify-center w-full rounded-md border border-gray-300 px-4 py-2 bg-white text-base leading-6 font-medium text-gray-700 shadow-sm hover:text-gray-500 focus:outline-none focus:border-blue-300 focus:shadow-outline-blue transition ease-in-out duration-150 sm:text-sm sm:leading-5"
                @click="onCancel"
              >
                Zrušit
              </button>
            </span>
          </div>
        </div>
      </div>
    </div>
  </v-portal>
</template>

<script>
import { Portal } from '@linusborg/vue-simple-portal';

export default {
  components: {
    'v-portal': Portal,
  },

  data: () => ({
    isShown: false,
    max: null,
    amount: null,
    resolve: null,
  }),

  methods: {
    async show({ max }) {
      this.isShown = true;
      this.max = max;

      await this.$nextTick();

      this.$refs.input.focus();

      return new Promise(resolve => {
        this.resolve = resolve;
      });
    },

    onConfirm() {
      if (!this.amount) return alert('Musíte vyplnit množství práce');
      if (this.amount < 1) return alert('Množství práce musí být více jak 0');
      if (this.amount >= this.max) return alert(`Množství práce nesmí být více nebo rovno jak ${this.max} ks`);

      this.isShown = false;

      this.resolve(this.amount);
    },

    onCancel() {
      this.isShown = false;

      this.resolve(null);
    },
  },
};
</script>
